import React, { PureComponent } from 'react';

import 'whatwg-fetch';
import * as queryString from 'query-string';

class Newsletter extends PureComponent {
  constructor(props) {
    super(props);

    this.state = { email: '' };
  }

  handleSubmit = (event) => {
    event.preventDefault();

    const { email } = this.state;
    const queryParams = queryString.stringify({ email });

    fetch(`https://script.google.com/macros/s/AKfycbyZu3JyC44b6fUd3agpwjUiThBQ4baAyY7q3BYGlUP8mN5u_yZb/exec?${queryParams}`, {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' }
    })
    .then(function(data) {
      console.log(data)
    })
    .catch(function(error) {
      console.log('request failed', error)
    });
  }

  handleInput = (event) => {
    this.setState({ [event.target.name]: event.target.value})
  }

  render() {
    return (
      <div className="c-newsletter">
        <form onSubmit={e => this.handleSubmit(e)}>
          <input
            type="email"
            name="email"
            id="email"
            value={this.state.email}
            placeholder="Your mail here"
            onChange={e => this.handleInput(e)}
            required
          />
          <button type="submit">Send</button>
        </form>
      </div>
    );
  }
}

export default Newsletter;



